@font-face{font-family:'spotify-sansserif';src:font_url("/assets/spotify-regular.woff") format("woff");font-weight:normal;font-style:normal}@font-face{font-family:'spotify-thin';src:font_url("/assets/spotify-thin.woff") format("woff");font-weight:normal;font-style:normal}body{background-color:#fbc3db}#nav{display:flex;justify-content:flex-start;border-bottom:2px, solid, #483e73}h1{font-family:'spotify-sansserif', Arial, sans-serif;-webkit-text-stroke-width:2px;-webkit-text-stroke-color:#483e73;font-size:80px;color:#fbc3db;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;margin:10px;margin-left:30px}#main-container{display:flex;justify-content:center;border-top:2px, solid, #483e73}#albumart{max-width:70%;height:auto}#artist-info{font-family:'spotify-thin', Arial, sans-serif}#genre-cloud>div{display:inline;font-family:'spotify-thin', Arial, sans-serif;border-radius:8px;background-color:#483e73;color:whitesmoke;padding:5px;margin:5px;opacity:66%}#data-bars{font-family:'spotify-thin', Arial, sans-serif;border-radius:8px;background-color:#483e73;color:whitesmoke;opacity:66%}#track-image{display:flex;align-items:center;flex-direction:column}
